.notes__input {
  position: relative;
  margin-top: 15px;
  width: 300px;

  & div:first-of-type {
    margin-left: 15px;
  }

  & textarea {
    resize: none;
    margin-top: 15px;
    width: 100%;
    height: 75px;
  }

  & .button {
    position: absolute;
    bottom: 10px;
    right: 0;
  }
}