.note {
  margin-top: 25px;
  padding: 20px;
  text-align: justify;
  width: 200px;
  position: relative;
  border: 1px solid black;
  z-index: 1;
  
  & .button {
    position: absolute;
    background-color: white;
    color: red;
    top: -20px;
    right: -17px;
  }
}

.note:not(.note:nth-of-type(3n + 1)) {
  margin-left: 15px;
}